.task-table{
    display: flex;
    justify-content: center;
    align-items: center;
}
    
table{
    border-spacing: 0;
    width: 800px;
    border-radius: 8px;
    overflow: hidden;
}

    
th{
    text-align: center;
    background-color: #1F1D36;
    color: #EEEEEE;
    border-bottom: 1px solid black;
}
    
td{
    text-align: center;
    background-color: #e0e0e0;
    border-bottom: 1px solid black;
}